import * as Table from './table';
export class List extends Table.Table {
    constructor(id, opts) {
        super(id, (() => {
            // @ts-ignore
            const tableOpts = opts;
            tableOpts.columns = [opts];
            tableOpts.headers = opts.header ? true : false;
            tableOpts.hover = opts.hover === false ? 'none' : 'row';
            return tableOpts;
        })());
    }
}
